<template>
    <v-layout class="bxMenu" align-center justify-space-around>
        <router-link to="/coach/session" class="bxMenu__bxIcons">
            <ICsession :width=IC.ICwidth :height=IC.ICheight />
            <v-btn depressed class="btnmenu text-capitalize">Sessions</v-btn>
        </router-link>
        <router-link to="/coach/coachtoken" class="bxMenu__bxIcons">
            <ICtoken :width=IC.ICwidth :height=IC.ICheight />
            <v-btn depressed class="btnmenu text-capitalize">Tokens/Credit</v-btn>
        </router-link>
        <router-link to="/coach/coachschedules" class="bxMenu__bxIcons">
            <ICbook :width=IC.ICwidth :height=IC.ICheight />
            <v-btn depressed class="btnmenu text-capitalize">Scheduling</v-btn>
        </router-link>
        <router-link to="/coach/script" class="bxMenu__bxIcons">
            <ICmaterial :width=IC.ICwidth :height=IC.ICheight />
            <v-btn depressed class="btnmenu text-capitalize">Coach Materials</v-btn>
        </router-link>
        <div class="bxMenu__bxIcons">
            <IChelp :width=IC.ICwidth :height=IC.ICheight />
            <v-btn depressed class="btnmenu text-capitalize">Help Center</v-btn>
        </div>
    </v-layout>
</template>
<script>
import ICsession from '../../../components/icons/OnlineLearningIcon.vue'
import ICtoken from '../../../components/icons/MoneyIcon.vue'
import ICbook from  '../../../components/icons/VideoCallIcon.vue'
import IChelp from '../../../components/icons/CustomerServiceIcon.vue'
import ICmaterial from '../../../components/icons/CoachMaterial'

export default {
  components:{
    ICsession, ICtoken, ICbook, IChelp, ICmaterial
  },
  data () {
    return {
      IC: {
        ICwidth: '150px',
        ICheight: '100%'
      }
    }
  }
}
</script>



