<template>
    <div class="BXupcomingSessions">
      <div class="view__header">
        <div class="view__header__title">
          <div>
            <h3>Upcoming Sessions</h3>
            <div class="blue__circle"></div>
          </div>
        </div>
      </div>
        <v-layout class="ContainUp" align-center justify-space-between>
            <div class="BXupcomingSessions__bxInfoSessions"  v-if="this.RoleID == 'student'">
                <div class="BXupcomingSessions__bxInfoSessions__NoUpcomming" v-if="this.DataUpcoming.length == 0">
                   You Have No Upcoming Sessions Today
                </div>
                <div 
                    class="BXupcomingSessions__bxInfoSessions__UpcomingInfo"
                    v-for="(n, index) in this.GET_DataUpcoming.slice(0,4)" :key="index"
                >
                    <div class="BXupcomingSessions__bxInfoSessions__UpcomingInfo--Upcoming">
                        <div class="UpAvatar" @click="Modal_Picture(n.avatar)">
                            <v-avatar size="42">
                                <img :src="n.avatar">
                            </v-avatar>
                        </div>

                         <div class="UpWith">
                             <p>Session With</p>
                             <p @click="Get_Username(n.username)"><router-link to="/student/view">{{n.coach_name}}</router-link></p>
                        </div>

                        <div class="UpDateTime">
                            <div class="UpDateTime__Date">
                                <v-icon small color="#375197">event</v-icon>
                                {{n.date | dateFormat}}
                            </div>
                            <!-- <div class="UpDateTime__Time">
                                {{n.start_time}} (UTC {{n.date | dateFormatUtc}})
                            </div> -->
                             <div class="UpDateTime__Time">
                                {{n.start_time}} ({{UserGMT}})
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="BXupcomingSessions__bxInfoSessions"  v-if="this.RoleID == 'coach'">
                <div class="BXupcomingSessions__bxInfoSessions__NoUpcomming" v-if="this.DataUpcoming.length == 0">
                   You Have No Upcoming Sessions Today
                </div>
                <div 
                    class="BXupcomingSessions__bxInfoSessions__UpcomingInfo"
                    v-for="(n, index) in this.GET_DataUpcoming" :key="index"
                >
                    <div class="BXupcomingSessions__bxInfoSessions__UpcomingInfo--Upcoming">
                        <div class="UpAvatar" @click="Modal_Picture(n.avatar)">
                            <v-avatar size="42">
                                <img :src="n.avatar">
                            </v-avatar>
                        </div>

                         <div class="UpWith">
                             <p>Session With</p>
                             <p @click="Get_Username(n.username)"><router-link to="/coach/view">{{n.student_name}}</router-link></p>
                        </div>

                        <div class="UpDateTime">
                            <div class="UpDateTime__Date">
                                <v-icon small color="#375197">event</v-icon>
                                {{n.date | dateFormat}}
                            </div>
                            <!-- <div class="UpDateTime__Time">
                                {{n.start_time}} (UTC {{n.date | dateFormatUtc}})
                            </div> -->
                             <div class="UpDateTime__Time">
                                {{n.start_time}} ({{UserGMT}})
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </v-layout>
        <v-dialog v-model="DialogUpAvatar" width="250" content-class="DialogUpAvatar">
            <v-card style="border-radius: 3px;">
                <v-avatar size="250">
                    <img :src="this.AvatarUpModal">
                </v-avatar>
            </v-card>
        </v-dialog>

    </div>
</template>

<script>
    import moment from 'moment'
    import {mapState} from 'vuex'
    export default{
        props:[
            'DataUpcoming',
            'RoleID'
        ],
        data () {
            return{
                DialogUpAvatar: false,
                AvatarUpModal : null,
                UserGMT: ''
            }
        },
        computed:{
            GET_DataUpcoming () {
                return this.DataUpcoming
            },
            
            GET_GMT(){
               const userGMT = this.$store.state.DashUpcomingSessions ?this.$store.state.DashUpcomingSessions.user_gmt:null

               return userGMT
            }
        },
        methods: {
            Get_Username(username){
                this.$store.commit('muttUpcomingSelect',{
                    Username : username
                })
            },
            Modal_Picture(avatar){
                this.DialogUpAvatar = true
                this.AvatarUpModal = avatar
            }
        },
        filters: {
            dateFormat: function (value) {
                if (value) {
                    return moment(String(value)).format('ddd, D MMMM YYYY')
                }
            },
            dateFormatUtc: function (value) {
                if (value) {
                    var newdate = moment.utc(value).local().format('Z')
                    return newdate
                }
            }
        },
        watch: {
            DialogUpAvatar(val){
                if(val == false){
                    this.AvatarUpModal = null
                }
            }
        },
        created() {
            this.UserGMT = this.GET_GMT
        },

    }
</script>

<style lang="scss">

</style>



